    title: "Stop Studying Programming",
    description:
      "There are programmers out there who spend more time trying to find the perfect tutorial than actually writing code.",
    content: `
    <p>The only way to learn new programming languages is by writing programs in it</p>
    <p> Dennis Ritchie</p><br>
    
    <p>To get started learning programming you just need four things.</p>
    <br>
    <p>Know about variables, conditions, loops, and functions.</p>
    <br>
    <p>After that you just need to write code.</p><br>
    
    <p>You don't need a roadmap for programming, you do not need a book, or another tutorial.</p><br>
    
    <p>Back in the day when a friend of mine started on programming he watched a 10 hour video tutorial on JavaScript.</p>
    <br>
    <p>After he finished the tutorial he came to me and said: I finished this tutorial then i went to open the code editor and couldn't write a single line of code.</p>
    <br>
    <p>I immediately said to him: exactly.</p><br>
    
    <p>There is a term for that in the word of programming, it is called tutorial hell.</p>
    <br>
    <p>And it is the worst mistake that anyone starting in programming can make.</p><br>
    
    <p>In fact, most people do not realize that, so let me explain the process of tutorial hell.</p><br>
    
    <p>Imagine that you finished your 10 hours course on JavaScript, great stuff indeed, then you open a new vscode window, and just like my friend, you just cannot write code.</p><br>
    
    <p>Tutorial hell is simply that, not being able to code without some guidance.</p><br>
    
    <h3>How to escape</h3><br>
    
    <p>Just like Dante trying to escape the hell (or purgatory), you first will need to know the following:</p>
    <br>
    <p>Functions, variables, loops, and conditions.</p>
    <br>
    <p>Really the basics of programming. With just that you can build anything you imagine.</p><br>
    
    <p>But how would I do that you might ask?</p>
    <br>
    <p>Well, as they say, through trial and error.</p>
    <br>
    <p>After getting the four basics out of the way you might want to pick a stack that you are interested in.</p><br>
    
    <p>If you like front-end, pick HTML, CSS, JavaScript, and later down the road a framework.</p>
    <br>
    <p>On the other hand, if it is back-end you can choose Python, PHP, NodeJS, Rust, C#, and so on. You can also choose game development or mobile development, there are so many options available.</p><br>
    
    <h3>The cool part</h3><br>
    
    <p>Now comes the most awesome part about all of this, you will actually write code. Wow, imagine that.</p><br>
    
    <p>Select some project that catches your attention and try to create on your own, or replicate if it is from some tutorial.</p><br>
    
    <p>And when you get stuck building the project, believe me you will, just search the problem and go again.</p><br>
    
    <p>Perhaps you might need to read some documentation, or even watch a video dedicated to the problem you are facing. No shame in that, because it is you that is actually doing the heavy lifting.</p><br>
    
    <p>You are not just copying code from some tutorial.</p><br>
    
    <p>By doing that you will be able to test your problem solving skills, and learn from actually doing the thing.</p><br>
    
    <p>Programming has this false feeling of learning by watching others doing, but at the end of the day you are not really learning.</p><br>
    
    <p>You will never learn how to play some sport, by watching others playing. And, at the end of the day, programming is the same thing.</p>
    `,
